@tailwind  base;
@tailwind  components;
@tailwind  utilities;

header ul li {
  @apply px-2;
}

.bg-primary {
  background: rgb(85 81 227);
}

.bg-secondary {
  background: #1f204e;
}

.mobile-nav {
  @apply fixed top-10 left-0 bg-gray-800 w-full h-1/4;
}

.mobile-nav  li{
  @apply  w-full h-full text-center border-b-2;
}

.projects-desc{
  @apply absolute left-0 right-0 bottom-[0px] top-0 bg-secondary opacity-0 duration-500 hover:opacity-100
}

.btn{
  @apply bg-primary px-6 py-2 font-bold hover:border-2 border-white rounded;
}

html {
  scroll-behavior: smooth;
}
